/* ✅ 전체 기본 스타일 */
body {
  margin: 0; /* 페이지 기본 여백 제거 */
  font-family: 'Noto Sans KR', sans-serif; /* 한글 웹폰트 적용 */
  background-color: #f9f9f9; /* 페이지 배경색: 밝은 회색 */
}

/* ✅ Header (상단 네비게이션 영역) */
.header {
  display: flex; /* 가로 정렬 */
  justify-content: space-between; /* 좌우 요소 간격 최대 */
  align-items: center; /* 세로 중앙 정렬 */
  padding: 10px 20px; /* 상하 10px, 좌우 20px 여백 */
  background-color: #fff; /* 배경: 흰색 */
  border-bottom: 1px solid #ddd; /* 아래쪽 경계선 */
  position: sticky; /* 스크롤 시 상단 고정 */
  top: 0; /* 화면 최상단에 고정 */
  z-index: 1000; /* 다른 요소 위에 표시 */
}

.header-logo img {
  height: 60px; /* 로고 이미지 높이 */
}

.header-nav a {
  margin: 0 10px; /* 메뉴 간격 좌우 10px */
  text-decoration: none; /* 밑줄 제거 */
  color: #333; /* 글자색: 진회색 */
  font-weight: 500; /* 글자 두께 중간 */
}

.header-nav .highlight {
  color: #4aad2c; /* 강조 색상 (쿠팡 초록 계열) */
  font-weight: bold; /* 굵게 */
}

/* ✅ Hero (메인 배너 영역) */
.hero {
  position: relative;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden; /* 비디오 넘치는 부분 잘림 */
  color: #fff; /* 기본 텍스트 색상 */
}

/* ✅ 동영상 스타일 */
.hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* 영상 비율 유지하며 꽉 채움 */
  z-index: -1; /* 텍스트 뒤로 */
  filter: brightness(70%); /* 영상 어둡게 → 텍스트 가독성 향상 */
}

/* ✅ Hero 버튼 스타일 */
.hero-btn {
  background-color: #4aad2c; /* 연두색 */
  color: #fff; /* 글자색 흰색 */
  padding: 15px 40px;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.hero-btn:hover {
  background-color: #6db75b; /* hover 시 더 진한 초록 */
  transform: scale(1.05); /* 부드러운 확대 효과 */
}

.hero-btn {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.hero-content h1 {
  font-size: 38px;
  font-weight: bold;
  margin-bottom: 15px;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.6); /* ✅ 제목 그림자 */
}

.hero-content p {
  font-size: 18px;
  margin-bottom: 25px;
  text-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5); /* ✅ 설명 그림자 */
}



/* ✅ 콘텐츠 */
.features {
  text-align: center;
  padding: 100px 20px;
  background: #fff;
}

.features-header {
  margin-bottom: 40px;
}

.features-header h2 {
  font-size: 28px;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.features-header p {
  font-size: 16px;
  color: #666;
}

.features-container {
  display: flex;
  justify-content: center;
  gap: 100px;
  flex-wrap: wrap; /* ✅ 반응형 대응 */
}

/* ✅ 이미지 hover 효과 */
.feature-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease; /* 확대 애니메이션 */
}

.feature-card:hover .feature-img {
  transform: scale(1.1); /* 이미지 살짝 확대 */
}

/* ✅ 카드 내 텍스트 색상 변경 (선택 사항) */
.feature-card:hover h3 {
  color: #72d365; /* hover 시 초록색 강조 */
}

.feature-bg {
  width: 180px;
  height: 180px;
  background-color: #f9f9f9;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 15px auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.feature-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.feature-card h3 {
  color: #4aad2c;
  font-size: 20px;
  margin-bottom: 8px;
}

.feature-card p {
  font-size: 15px;
  color: #555;
}



/*  Success Lecture Section */
.success-lecture {
  background: #f8f8f8;
  text-align: center;
  padding: 60px 20px 100px 20px; /* 하단 넉넉하게 */
}

.success-lecture h2 {
  font-size: 28px;
  font-weight: bold;
  margin: 0; /* ✅ 기본 margin 제거 */
}

.success-lecture h2 .highlight {
  color: #4aad2c;
}

.success-lecture p {
  color: #777;
  font-size: 16px;
  margin: 4px 0 40px; /* ✅ 위쪽 간격 최소화, 아래쪽만 유지 */
}

.lecture-video {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  flex-wrap: wrap;
  margin-top: 10px; /* ✅ 위쪽 영상과 살짝 간격 */
}

.lecture-player {
  width: 480px;
  max-width: 100%;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.lecture-info {
  max-width: 320px;
  text-align: left;
}

.underline-title {
  font-size: 22px;
  font-weight: bold;
  color: #333;
  border-bottom: 3px solid #4aad2c;
  display: inline-block;
  padding-bottom: 4px;
  margin-bottom: 12px;
}

.speaker {
  font-size: 16px;
  font-weight: 500;
  color: #4aad2c;
  margin-bottom: 6px;
}

.desc {
  font-size: 14px;
  color: #555;
  margin-top: 0;
}


/* ✅ Footer (하단 영역) */
.footer {
  background: #333; /* 배경: 진한 회색(거의 검정) */
  color: #fff; /* 글자색: 흰색 */
  text-align: center; /* 중앙 정렬 */
  padding: 20px; /* 여백 */
}
